* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  font-weight: 500;
}

li{
  list-style: none;
}


.country-list{
  padding: 0; 
  margin: 0;
}

.country-info{
padding: 0;
margin: 0;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}



.country-item{
  display: flex;
  align-items: center;
}

.flag{
  margin-right: 10px;
}

.flex{
  display: flex;
  align-items: center;
}

.name{
margin: 0;

  font-size: 40px;
  font-weight: 600;

}